html,body, #root {
    height:100%;
}
div[role=tooltip]{
   max-height: 100px;
    z-index: 2000;
}

.hoverable:hover {
    background-color: rgb(70 70 70) !important;
}

.wrapper > div {
    border: 1px solid;
}

.child {
    display: flex;
}

.child div {
    flex-grow: 1;
    width: 0;
}

.wrapper {
    display: inline-block;
}

/*Fixes dialogs not being centered*/
div[role=dialog]{
    margin-bottom: 150px;
}

